import Breadcrumb from "@/app/components/BreadCrumb";
import { Fragment } from "react";
import { LuRocket } from "react-icons/lu";
import { IoBagOutline, IoBookOutline } from "react-icons/io5";
import { FaArrowRightLong } from "react-icons/fa6";
const cardData = [
  {
    icon: <LuRocket />,
    heading: "Sales",
    text: "Contact our sales team to discuss your needs and how we can help.",
  },
  {
    icon: <IoBagOutline />,
    heading: "Collaborations & Partnerships",
    text: "Contact our partnerships team to explore how we can work together.",
  },
  {
    icon: <IoBookOutline />,
    heading: "Media Inquiries",
    text: "For media inquiries, interviews, or press materials, reach out to our press team.",
  },
];

export default function Contact() {
  return (
    <Fragment>
      <section className="  flex flex-row max-lg:hidden items-center  px-[5%] justify-start text-white bg-black  items-start ">
        <Breadcrumb />
      </section>
      {/* Get in touch */}
      <section className="  flex flex-row max-lg:flex-col items-center lg:h-[30vh] max-lg:h-[100vh] max-lg:pt-70 px-[5%] justify-evenly text-white bg-black  items-start ">
        <div className="breadCrum w-1/4 ">
          <h4 className="text-[#0a49eb] text-[16px]">Contact</h4>
        </div>
        <div className="right w-3/4">
          <h2 className="text-[48px]  leading-[60px]">Get in touch</h2>
          <h4 className="text-[#a5acc0] text-[24px] leading-[36px]">
            Want to get in touch? We'd love to hear from you. Here's how you can
            reach us.
          </h4>
        </div>
      </section>
      {/* card */}
      <section className="  flex flex-row max-lg:flex-col gap-6 items-center   max-lg:h-[100vh] max-lg:pt-70 px-[5%] justify-evenly text-white bg-black  items-start ">
        {cardData.map((item, index) => {
          return (
            <div
              key={index}
              className=" border border-[#2f3030ff] flex flex-col justify-between md:h-[250px] transition-colors duration-300 ease-in-out hover:border-[#0a49eb] rounded-[10px] w-1/3 p-8 cursor-pointer "
            >
              <span className="text-[#0a49eb] text-[24px] ">{item.icon}</span>
              <h4 className="font-medium text-[20px] leading[30px]">
                {item.heading}
              </h4>

              <p className="w-full text[20px] leading[30px]">{item.text}</p>
              <div className="flex justify-end w-full ">
                <FaArrowRightLong />
              </div>
            </div>
          );
        })}
      </section>

      <section className="  flex flex-col max-lg:flex-col gap-6 items-center h-[100vh] max-lg:pt-70 px-[5%] justify-center text-white bg-black  items-center">
        <div className="right space-y-8 w-3/4">
          <h2 className="text-[48px]  leading-[60px]">
            Find us in the Heart of San Diego.
          </h2>
          <h4 className="text-[#a5acc0] text-[24px] leading-[36px]">
            Weâ€™re in the Gaslamp Quarter, surrounded by good food, good vibes,
            and great work.
          </h4>
        </div>
      </section>
    </Fragment>
  );
}
